html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  overflow-x: hidden;
  font-family: "Poppins", sans-serif;
}

img {
  max-width: 100%;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.container {
  margin: 0 auto;
  width: 95%;
  max-width: 130rem;
}

.section {
  padding: 5rem 0;
}

.body.hidden {
  overflow: hidden;
}

.overlay {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 99;
}

.fa-whatsapp {
  color: #fff;
  transition: 0.2s ease-in-out;
}
.fa-whatsapp:hover {
  color: #25d366;
}

.fa-facebook-square {
  color: #fff;
  transition: 0.2s ease-in-out;
}
.fa-facebook-square:hover {
  color: #2C88FF;
}

.fa-map-marker-alt {
  color: #fff;
  transition: 0.2s ease-in-out;
}
.fa-map-marker-alt:hover {
  color: #DE432A;
}

.fa-instagram {
  color: #fff;
  transition: 0.2s ease-in-out;
}
.fa-instagram:hover {
  color: #C13584;
}

.fa-envelope {
  color: #fff;
  transition: 0.2s ease-in-out;
}
.fa-envelope:hover {
  color: #FFB805;
}

.fa-phone-alt {
  font-size: 1.3rem;
}

.info-bar {
  margin-bottom: 3rem;
  background-color: #000;
  color: #fff;
  font-size: 3.5vw;
  z-index: 999;
}
@media (min-width: 480px) {
  .info-bar {
    font-size: 1.6rem;
  }
}
@media (min-width: 1024px) {
  .info-bar {
    margin-bottom: 2rem;
  }
}
.info-bar p {
  margin: 0;
}
.info-bar .info {
  display: flex;
  gap: 1rem;
}

.content-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 0;
}
.content-bar .help a {
  color: #fff;
}
.content-bar .location {
  display: none;
}
@media (min-width: 1024px) {
  .content-bar .location {
    cursor: pointer;
    display: flex;
    align-items: center;
  }
}

.social-media .logos {
  display: none;
}
@media (min-width: 1024px) {
  .social-media .logos {
    margin-right: 0.5rem;
    display: flex;
    gap: 1rem;
  }
}
.social-media .phone-info {
  margin-left: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.header {
  background-color: #2e005a;
  padding-bottom: 3rem;
  position: sticky;
  z-index: 90;
  top: 0;
}
@media (min-width: 1024px) {
  .header {
    padding-bottom: 2rem;
  }
}

.barra-header {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 2rem;
  align-items: center;
}
@media (min-width: 1024px) {
  .barra-header {
    display: flex;
    justify-content: space-between;
  }
}

.logo {
  justify-self: center;
  width: 25rem;
  grid-column: 3/5;
}
@media (min-width: 768px) {
  .logo {
    flex: 0 0 22rem;
  }
}

.hamburger-menu {
  grid-column: 1/2;
  justify-self: self-start;
}

.search-bar {
  grid-column: 2/5;
  border-radius: 3rem;
  padding: 1rem 2rem;
  border-style: none;
  outline: none;
  transition: 0.2s ease-in-out;
}
@media (min-width: 480px) {
  .search-bar {
    grid-column: 2/6;
  }
}
@media (min-width: 1024px) {
  .search-bar {
    width: 13vw;
  }
}
.search-bar:hover {
  background-color: #957DAD;
}
.search-bar::-webkit-input-placeholder {
  font-weight: 400;
  color: #957DAD;
}
.search-bar::-moz-placeholder {
  font-weight: 400;
  color: #957DAD;
}
.search-bar::-ms-input-placeholder {
  font-weight: 400;
  color: #957DAD;
}
.search-bar:hover::-webkit-input-placeholder {
  color: #E0BBE4;
}
.search-bar:hover::-moz-placeholder {
  color: #E0BBE4;
}
.search-bar:hover:-moz-placeholder {
  color: #E0BBE4;
}

.user-cart {
  display: flex;
  align-items: center;
  gap: 2.5rem;
  justify-self: end;
  grid-column: 6/8;
}
@media (min-width: 480px) {
  .user-cart {
    gap: 1rem;
  }
}
@media (min-width: 768px) {
  .user-cart {
    gap: 3rem;
  }
}
.user-cart a {
  color: #fff;
  font-size: 3rem;
}

.user #user-icon {
  position: relative;
  transition: 0.2s ease-in-out;
  font-size: 3rem;
  color: #fff;
  cursor: pointer;
}
.user #user-icon:hover {
  color: #F512F1;
}

.shopping-cart {
  position: relative;
  transition: 0.2s ease-in-out;
}
.shopping-cart:hover .items-cart {
  color: #fff;
}
.shopping-cart:hover .fa-shopping-cart {
  color: #F512F1;
}
.shopping-cart .fa-shopping-cart {
  transition: ease-in-out 0.2s;
  color: #fff;
  font-size: 3rem;
}
.shopping-cart .items-cart {
  color: #F512F1;
  text-align: center;
  font-size: 1.3rem;
  position: absolute;
  font-weight: 700;
  border-radius: 50%;
  line-height: 1.5;
  top: 0.2rem;
  right: 0;
  left: 0.5rem;
  transition: 0.2s ease-in-out;
}

.hamburger-menu {
  cursor: pointer;
}
@media (min-width: 1024px) {
  .hamburger-menu {
    display: none;
  }
}
.hamburger-menu .line {
  height: 0.3rem;
  width: 2.5rem;
  background-color: #fff;
  margin: 0.5rem;
  border-radius: 1rem;
  transition: all 0.3s ease;
}

.toggle.line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.toggle.line2 {
  opacity: 0;
}

.toggle.line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}

.login-window {
  box-shadow: 0rem 0rem 0.4rem #444;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  padding: 1rem;
  border-radius: 0.5rem;
  position: absolute;
  top: 5rem;
  right: -6rem;
  width: 35rem;
  height: 20rem;
  background-color: #957DAD;
  display: none;
  color: #fff;
  z-index: 1000;
}
@media (min-width: 480px) {
  .login-window {
    right: -4rem;
  }
}
@media (min-width: 1024px) {
  .login-window {
    top: 6rem;
    width: 40rem;
    height: 30rem;
  }
}
.login-window .buttons {
  display: flex;
  justify-content: space-evenly;
}
.login-window .buttons .login-button {
  color: #fff;
  font-size: 2rem;
  background-color: #2e005a;
  padding: 1rem;
  border-radius: 0.5rem;
  transition: 0.2s linear;
}
.login-window .buttons .login-button:hover {
  background-color: #fff;
  color: #2e005a;
}
.login-window .close-button {
  font-size: 2.5rem;
  cursor: pointer;
  position: absolute;
  top: 1rem;
  right: 1rem;
}
.login-window .text-user {
  font-size: 3rem;
  font-weight: 700;
  text-align: center;
  display: block;
  margin: 2rem 0 5rem 0;
}
.login-window .text-user .name-user {
  display: block;
}
.login-window.open {
  animation: open 0.3s ease-in-out forwards;
}
.login-window.close {
  animation: close 0.3s forwards;
}
@keyframes open {
  0% {
    transform: scale(0);
    transform-origin: 100% 0%;
    opacity: 1;
  }
  100% {
    transform: scale(1);
    transform-origin: 100% 0%;
    opacity: 1;
  }
}
@keyframes close {
  100% {
    transform: scale(0);
    transform-origin: 100% 0%;
    opacity: 1;
  }
  0% {
    transform: scale(1);
    transform-origin: 100% 0%;
    opacity: 1;
  }
}

.bar-nav .main-nav {
  font-weight: 700;
  position: fixed;
  width: 70%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #2e005a;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  transition: transform ease 0.5s;
  transform: translate(-100%, 0);
  padding: 7rem 0;
  z-index: 200;
}
@media (min-width: 1024px) {
  .bar-nav .main-nav {
    padding: 3rem 0;
    display: flex;
    transform: translate(0, 0);
    position: static;
    transition: none;
    flex-direction: row;
    width: auto;
    justify-content: center;
    gap: 5rem;
  }
}
.bar-nav .main-nav a {
  height: 15vh;
  text-transform: uppercase;
  font-size: 1.6rem;
  color: #fff;
  transition: 0.2s ease-in-out;
}
.bar-nav .main-nav a:hover {
  color: #F512F1;
}
@media (min-width: 1024px) {
  .bar-nav .main-nav a {
    height: auto;
  }
}
.bar-nav .main-nav i {
  font-size: 1.3rem;
}

.bar-nav .main-nav.is-active {
  transform: translate(0, 0);
  -webkit-box-shadow: 10px 0px 23px -12px rgba(0, 0, 0, 0.86);
  -moz-box-shadow: 10px 0px 23px -12px rgba(0, 0, 0, 0.86);
  box-shadow: 10px 0px 23px -12px rgba(0, 0, 0, 0.86);
}