.hamburger-menu {
    cursor: pointer;
    @include desktop{
        display: none;
    }

    .line {
        height: .3rem;
        width: 2.5rem;
        background-color: $white;
        margin: .5rem;
        border-radius: 1rem;
        transition: all 0.3s ease;
    }
}
.toggle.line1 {
    transform: rotate(-45deg) translate(-5px, 6px);
    
}
.toggle.line2 {
    opacity: 0;
}
.toggle.line3 {
    transform: rotate(45deg) translate(-5px, -6px);
}